:root {
  --font-title: "Montserrat", sans-serif;
  --font-family: "Inter", sans-serif;
  --content-width: 1140px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light: #fff;
  --dark: #202124;
  --dark-shark: #2c2d2e;
  --grey-dark: #3d3e40;
  --grey: #818287;
  --grey-light: #e3e3e7;
  --smoke: #d3d4db;
  --accent: #cfe741;
  --main: #f4f4f5;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable value-keyword-case */
@font-face {
  font-family: "Inter";
  src: url("../fonts/../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/../fonts/Inter-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/Montserrat-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  line-height: 24px;
  background-color: var(--main);
  color: var(--dark);
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.title {
  font-family: var(--font-title);
  font-size: 24px;
  font-weight: 600;
  line-height: 32px;
  color: var(--dark);
  margin: 0;
}
.title--page {
  font-size: 48px;
  line-height: 60px;
}
.title--section {
  font-size: 36px;
  line-height: 44px;
}
.title--big {
  font-size: 20px;
  line-height: 30px;
}
.title--s {
  font-size: 18px;
  line-height: 28px;
}
.title--xs {
  font-size: 16px;
  line-height: 24px;
}

.link {
  display: block;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: var(--light);
  border-radius: 10px;
  border: 1px solid var(--light);
  background-color: transparent;
  padding: 12px 18px;
}
.link--big {
  font-size: 18px;
  line-height: 28px;
  padding: 14px 24px;
}
.link--small {
  font-size: 14px;
  line-height: 20px;
  padding: 6px 14px;
}
.link--green {
  color: var(--dark);
  background-color: var(--accent);
  border-color: var(--accent);
}
.link--grey {
  color: var(--light);
  background-color: var(--dark);
  border-color: var(--dark);
}

.nav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav__link {
  font-size: 14px;
  font-weight: 600;
  color: var(--light);
}

.accordion--features .ac {
  border: none;
  border-bottom: 1px solid #eee;
  background-color: transparent;
}
.accordion--features .ac-text {
  font-family: var(--font-family);
  font-size: 16px;
  color: var(--dark);
  padding: 0 0 15px 0;
}
.accordion--features .ac-title {
  color: var(--grey);
  transition: color 0.4s ease-in-out;
}
.accordion--features .ac-trigger {
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}
.accordion--features .ac-trigger svg {
  flex-shrink: 0;
  width: 25px;
  height: 25px;
  border: 2px solid var(--grey);
  border-radius: 100%;
  fill: none;
  stroke: var(--grey);
  stroke-linecap: round;
  stroke-width: 2px;
  rotate: 90deg;
  transition: rotate 0.4s ease-in-out, border 0.4s ease-in-out, stroke 0.4s ease-in-out;
}
.accordion--features .ac:hover .ac-title {
  color: var(--dark);
}
.accordion--features .ac:hover svg {
  border: 2px solid var(--dark);
  stroke: var(--dark);
}
.accordion--features .is-active .ac-trigger svg {
  rotate: -90deg;
  border: 2px solid var(--dark);
  stroke: var(--dark);
}
.accordion--features .is-active .ac-title {
  color: var(--dark);
}

.header {
  background-color: var(--dark);
}
.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 0;
}
.header__bottom {
  padding: 15px 25px;
  border: 1px solid #fff;
  border-radius: 10px;
}

.hero {
  background-color: var(--dark);
  border-radius: 0 0 75px 75px;
  margin-bottom: 120px;
}
.hero__container {
  padding-top: 80px;
  padding-bottom: 80px;
}
.hero__content {
  max-width: 800px;
}
.hero__title {
  color: var(--light);
  margin-bottom: 10px;
}
.hero__subtitle {
  font-size: 24px;
  font-weight: 600;
  line-height: 32px;
  color: var(--light);
  margin-bottom: 35px;
}
.hero__wrapp-btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 500px;
}
.about {
  margin-bottom: 120px;
}
.about__container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 50px;
}
.about__content {
  grid-column: 1/span 5;
  display: flex;
  flex-direction: column;
  padding: 30px;
  background-color: var(--light);
  border-radius: 25px;
}
.about__title {
  margin-bottom: 20px;
}
.about__description {
  margin-bottom: 20px;
}
.about__link {
  text-align: center;
  margin-top: auto;
}
.about__text-link {
  color: inherit;
}
.about__img {
  grid-column: 6/span 7;
}
.about__image {
  border-radius: 25px;
  height: 100%;
}

.services {
  margin-bottom: 120px;
}
.services__title {
  margin-bottom: 36px;
}
.services-card__title {
  margin-bottom: 20px;
}
.services-card__list {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 30px;
  grid-template-areas: "a a a b b b" "c c c b b b" "d d d d d d" "e e f f g g" "h h h h h h";
}
.services-card__item {
  background-color: var(--light);
  border-radius: 15px;
  padding: 25px;
  grid-column: 3;
  transition: transform 0.2s ease;
}
.services-card__item:nth-child(1) {
  grid-area: a;
}
.services-card__item:nth-child(2) {
  grid-area: b;
}
.services-card__item:nth-child(3) {
  grid-area: c;
}
.services-card__item:nth-child(4) {
  grid-area: d;
}
.services-card__item:nth-child(5) {
  grid-area: e;
}
.services-card__item:nth-child(6) {
  grid-area: f;
}
.services-card__item:nth-child(7) {
  grid-area: g;
}
.services-card__item:nth-child(8) {
  grid-area: h;
}
.services-card__item:hover {
  transform: translateY(-10px);
}
.services-card__text:not(:last-child) {
  margin-bottom: 15px;
}

.features {
  margin-bottom: 120px;
}
.features__title {
  margin-bottom: 36px;
}

.stack {
  margin-bottom: 120px;
}
.stack__title {
  margin-bottom: 36px;
}
.stack__slide {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: grabbing;
}